@font-face {
  font-family: Poppins;
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/Poppins-Regular.woff2") format("woff2"), url("../fonts/Poppins-Regular.woff") format("woff");
}
@font-face {
  font-family: Poppins;
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src: url("../fonts/Poppins-Medium.woff2") format("woff2"), url("../fonts/Poppins-Medium.woff") format("woff");
}
@font-face {
  font-family: Poppins;
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: url("../fonts/Poppins-Bold.woff2") format("woff2"), url("../fonts/Poppins-Bold.woff") format("woff");
}
html {
  box-sizing: border-box;
  text-size-adjust: none;
  -webkit-tap-highlight-color: transparent;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

html,
body {
  min-width: 320px;
  background-color: #fff;
  color: #000;
  font-family: Poppins;
  font-size: 18px;
  margin: 0;
  min-height: 100%;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

img {
  width: 100%;
  height: auto;
  max-width: 100%;
}

.visually-hidden {
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
  white-space: nowrap;
}

.root {
  position: relative;
}

.header {
  width: 100%;
  padding: 20px 0;
}
@media (min-width: 768px) {
  .header {
    padding: 40px 0 20px;
  }
}
@media (min-width: 1028px) {
  .header {
    position: absolute;
    top: 0;
    left: 0;
    padding: 60px 0 30px;
  }
}
.header__wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
@media (min-width: 768px) {
  .header__wrapper {
    gap: 40px;
  }
}
@media (min-width: 1028px) {
  .header__logo {
    margin-right: 30px;
  }
}

.header--open .burger > span {
  height: 0;
}

.header--open .burger > span::before {
  transform: rotate(45deg);
}

.header--open .burger > span::after {
  transform: rotate(-45deg);
}

.header--open .nav--animate {
  position: absolute;
  z-index: 20;
  transition: transform 0.5s ease;
  transform: translateX(0%);
}

.header--open .nav__wrap {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  backdrop-filter: blur(15px);
  z-index: 1;
}

.grid-block {
  display: block;
  margin: 0 auto;
  width: 100%;
  max-width: 1400px;
  padding-left: 20px;
  padding-right: 20px;
}
@media (min-width: 500px) {
  .grid-block {
    padding-left: 40px;
    padding-right: 40px;
  }
}
@media (min-width: 1028px) {
  .grid-block {
    padding-left: 60px;
    padding-right: 60px;
  }
}
@media (min-width: 1200px) {
  .grid-block {
    padding-left: 110px;
    padding-right: 110px;
  }
}
@media (min-width: 1400px) {
  .grid-block {
    padding-left: 200px;
    padding-right: 200px;
  }
}

@media (min-width: 1028px) {
  .logo-mobile {
    display: none;
  }
}

@media (max-width: 1027px) {
  .logo-desktop {
    display: none;
  }
}

.burger {
  display: flex;
  position: relative;
  top: 4px;
  padding: 0;
  width: 25px;
  height: 25px;
  z-index: 5;
  cursor: pointer;
  border: none;
  background-color: transparent;
}
@media (min-width: 768px) {
  .burger {
    display: none;
  }
}

.burger > span,
.burger > span::before,
.burger > span::after {
  position: absolute;
  display: block;
  width: 100%;
  height: 2px;
  background-color: #000;
  transition: 0.2s;
}

.burger > span:hover,
.burger > span:hover::before,
.burger > span:hover::after {
  background-color: #d3b47f;
}

.burger > span {
  top: 10px;
}

.burger > span::before {
  content: "";
  transform: translateY(-8px);
}

.burger > span::after {
  content: "";
  transform: translateY(8px);
}

.nav {
  top: 90px;
  left: 0;
}
@media (max-width: 767px) {
  .nav {
    position: fixed;
    background-color: #ebe9e9;
    width: 100%;
    overflow: auto;
    z-index: 20;
    transform: translateX(-100%);
    height: 100%;
  }
}
@media (min-width: 768px) {
  .nav {
    display: flex;
    align-items: center;
  }
}
.nav__link {
  font-size: 16px;
  line-height: 1.125;
  text-decoration: none;
  color: #000;
  cursor: pointer;
}
@media (min-width: 768px) {
  .nav__link {
    padding: 0;
    font-size: 14px;
    line-height: 1.5714285714;
  }
}
@media (min-width: 1028px) {
  .nav__link {
    padding: 0;
    font-size: 16px;
    line-height: 1.5;
    color: #fff;
  }
}
.nav__link:hover {
  color: #d3b47f;
}
@media (min-width: 768px) {
  .nav__phone {
    margin-left: 20px;
  }
}
@media (min-width: 800px) {
  .nav__phone {
    margin-left: 40px;
  }
}

.nav-list {
  display: flex;
  flex-direction: column;
  margin: 0;
  padding: 0;
  list-style: none;
}
@media (min-width: 768px) {
  .nav-list {
    flex-direction: row;
  }
}
.nav-list__item {
  border-bottom: 2px solid #acacac;
}
.nav-list__item:last-child {
  margin: 0;
}
@media (min-width: 768px) {
  .nav-list__item {
    margin-right: 14px;
    border-bottom: none;
  }
}
@media (min-width: 1200px) {
  .nav-list__item {
    margin-right: 45px;
  }
}
.nav-list__link {
  display: block;
}
@media (max-width: 767px) {
  .nav-list__link {
    padding: 15px;
  }
}

.phone {
  display: flex;
  align-items: center;
  gap: 5px;
}
@media (max-width: 767px) {
  .phone {
    padding: 15px;
  }
}

.intro {
  min-width: 320px;
  padding: 20px 0;
}
@media (min-width: 1028px) {
  .intro {
    padding: 264px 0 100px;
    background-image: url("../../src/img/background.png");
    background-repeat: no-repeat;
    background-position: top center;
    background-size: cover;
  }
}
.intro__hours {
  margin: 0 0 15px;
  font-size: 16px;
  line-height: 1.125;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .intro__hours {
    margin: 0 0 24px;
  }
}
@media (min-width: 1028px) {
  .intro__hours {
    color: #fff;
  }
}
.intro__wrapper {
  margin-top: 20px;
}
@media (min-width: 768px) {
  .intro__wrapper {
    display: flex;
    align-items: baseline;
    justify-content: space-between;
    margin-top: 40px;
  }
}
.intro__btn {
  margin-bottom: 30px;
}
.intro__gallery {
  margin-top: 30px;
}
@media (min-width: 1028px) {
  .intro__gallery {
    display: none;
  }
}

.main-title {
  margin: 0;
  font-size: 40px;
  line-height: 1;
  font-weight: 700;
}
@media (min-width: 1028px) {
  .main-title {
    font-size: 64px;
    color: #fff;
  }
}
@media (min-width: 1200px) {
  .main-title {
    font-size: 64px;
    line-height: 1.375;
  }
}

.btn {
  display: inline-block;
  max-width: 270px;
  width: 100%;
  padding: 20px;
  font-size: 18px;
  line-height: 0.75;
  text-align: center;
  text-decoration: none;
  background-color: #3a4574;
  color: #fff;
  border: none;
  cursor: pointer;
  transition: background-color 0.5s ease;
}
@media (min-width: 768px) {
  .btn {
    padding: 30px;
  }
}
.btn:hover {
  background-color: #d3b47f;
  color: #000;
}

.social-list {
  display: flex;
  margin: 0;
  padding: 0;
  list-style: none;
  gap: 15px;
}
.social-list__item {
  width: 48px;
  height: 48px;
}
.social-list__link {
  display: flex;
  justify-content: center;
  font-size: 16px;
  font-weight: 600;
  line-height: 1.125;
  text-decoration: none;
  color: #000;
  border: 1px solid #000;
  border-radius: 50%;
  padding: 12px 13px;
}
.social-list__link:hover {
  border: 1px solid #d3b47f;
  color: #d3b47f;
}
@media (min-width: 1028px) {
  .social-list__link {
    color: #fff;
    border: 1px solid #fff;
  }
}

.gallery-block img {
  width: 100%;
  height: auto;
}

.menu {
  min-width: 320px;
  padding: 20px 0;
}
@media (min-width: 768px) {
  .menu {
    padding: 60px 0;
  }
}
.menu__wrapper {
  display: flex;
  flex-direction: column;
}
@media (min-width: 800px) {
  .menu__wrapper {
    flex-direction: row;
    gap: 20px;
  }
}

.menu-list {
  display: flex;
  flex-direction: column;
  margin: 0;
  padding: 0;
  list-style: none;
}
@media (min-width: 800px) {
  .menu-list {
    width: 50%;
  }
}
.menu-list__item {
  margin-bottom: 20px;
  padding: 15px 0;
  border-bottom: 1px solid #e7e7e7;
}
.menu-list__day {
  margin: 0 0 15px;
  font-size: 20px;
  font-weight: 600;
  line-height: 1.2222222222;
}

.menu-dish {
  display: flex;
  align-items: center;
}
.menu-dish__title {
  margin: 0;
  font-size: 16px;
  line-height: 1;
}
.menu-dish__title--first {
  margin-right: 12px;
}
.menu-dish__title--second {
  margin-left: 12px;
}

.photo-block {
  display: flex;
  gap: 10px;
  position: relative;
}
@media (min-width: 800px) {
  .photo-block {
    width: 50%;
  }
}
.photo-block::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 80%;
  height: 80%;
  background-color: transparent;
  border: 2px solid #d3b47f;
  transform: translate(-50%, -50%);
  z-index: 1;
}
@media (min-width: 800px) and (max-width: 1028px) {
  .photo-block::before {
    top: 32%;
    height: 56%;
  }
}
.photo-block__img {
  z-index: 5;
}
.photo-block__img:first-child {
  margin-top: 150px;
}
@media (min-width: 800px) {
  .photo-block__img:last-child {
    margin-top: 15px;
  }
}

.reviews {
  min-width: 320px;
  padding: 20px 0 40px;
}
@media (min-width: 500px) {
  .reviews {
    padding: 40px 0;
  }
}
@media (min-width: 768px) {
  .reviews {
    padding: 60px 0;
    background-color: #3a4574;
  }
}
.reviews__wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.reviews__icon {
  margin-bottom: 34px;
}

.swiper-button-prev,
.swiper-button-next {
  top: 47%;
  color: #d3b47f;
}

.reviews-list {
  height: auto;
  margin: 0;
  padding: 0;
  list-style: none;
}
.reviews-list__item {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.reviews-list__title {
  margin: 0 0 20px;
  font-size: 26px;
  font-weight: 600;
  line-height: 1;
  text-align: center;
  color: #d3b47f;
}
@media (max-width: 1027px) {
  .reviews-list__title {
    max-width: 410px;
  }
}
@media (min-width: 768px) {
  .reviews-list__title {
    font-size: 36px;
    line-height: 1.1111111111;
  }
}
.reviews-list__text {
  max-width: 720px;
  margin: 0 0 40px;
  padding: 0 55px;
  font-size: 14px;
  line-height: 1.5714285714;
  text-align: center;
}
@media (min-width: 768px) {
  .reviews-list__text {
    font-size: 16px;
    line-height: 2.5;
    color: #fff;
  }
}
.reviews-list__name {
  margin: 0;
  font-size: 22px;
  line-height: 1;
  text-transform: uppercase;
  letter-spacing: 5px;
  color: #d3b47f;
}

.modal {
  min-width: 320px;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  margin-bottom: auto;
  background: rgba(0, 0, 0, 0.5019607843);
  opacity: 0;
  pointer-events: none;
  transition: 0.5s all scale(0);
  backdrop-filter: blur(15px);
  z-index: 20;
}
.modal__inner {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) scale(0);
  width: 100%;
  max-width: 500px;
  padding: 30px 40px 60px;
  background: #fff;
  transition: 0.5s all;
}
.modal__top {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 40px;
}
.modal__close {
  display: block;
  width: 25px;
  height: 25px;
  cursor: pointer;
}
.modal__close span {
  position: relative;
  font-size: 0;
}
.modal__close span:hover {
  background-color: #d3b47f;
}
.modal__close span::before, .modal__close span::after {
  content: "";
  position: absolute;
  top: -9px;
  display: block;
  width: 26px;
  height: 2px;
  border-radius: 2px;
  background-color: #000;
}
.modal__close span::before {
  transform: rotate(45deg);
}
.modal__close span::after {
  transform: rotate(-45deg);
}

.modal--active {
  opacity: 1;
  pointer-events: all;
  transition: 0.5s all;
}

.modal__inner--active {
  transform: translate(-50%, -50%) scale(1);
  transition: 0.5s all;
}

.modal-form {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0;
}
.modal-form__input {
  width: 100%;
  height: 50px;
  padding: 10px 20px;
  font-size: 18px;
  border: 1px solid #ccc;
  outline: none;
}
.modal-form__group {
  width: 100%;
  margin-bottom: 45px;
}
.modal-form__btn {
  width: 100%;
}
.modal-form__success--active {
  font-size: 20px;
}

.input-error {
  border: 1px solid #b30505;
}

.message-success {
  display: none;
}

.message-error {
  display: none;
}